"use client";

import { useState } from "react";
import { Users, ShieldCheck, Gamepad2, Globe2, ChevronDown, X } from "lucide-react";

export function AboutSection() {
  const [open, setOpen] = useState(false);

  const features = [
    { icon: <Users className="w-8 h-8 text-yellow-400" />, title: "Komunitas Ramah" },
    { icon: <ShieldCheck className="w-8 h-8 text-yellow-400" />, title: "Anti-Cheat" },
    { icon: <Gamepad2 className="w-8 h-8 text-yellow-400" />, title: "Versi Terbaru" },
    { icon: <Globe2 className="w-8 h-8 text-yellow-400" />, title: "Bebas Lag" },
  ];

  return (
    <section className="relative py-24 bg-[#050B1A] text-white overflow-hidden">
      <div className="container mx-auto px-6 lg:px-12 grid grid-cols-1 lg:grid-cols-2 gap-12 items-center">
        {/* Left Side */}
        <div className="space-y-6">
          <h2 className="text-4xl md:text-5xl font-bold text-yellow-400">
            Tentang Kaizen Network
          </h2>

          <p className="text-gray-400 text-lg leading-relaxed max-w-lg">
            Kaizen Network adalah server Minecraft Indonesia yang menghadirkan pengalaman bermain terbaik
            untuk semua pemain. Kami menawarkan berbagai fitur seru, unik, dan lucu, serta sub-server seperti
            <span className="text-white"> Survival Mix</span>, <span className="text-white">Tycoon</span>,
            dan <span className="text-white">Creative</span>, serta mode baru yang akan datang.
          </p>

          <button
            onClick={() => setOpen(true)}
            className="flex items-center gap-2 text-yellow-400 font-semibold hover:underline underline-offset-4 transition"
          >
            Baca selengkapnya <ChevronDown className="w-4 h-4" />
          </button>
        </div>

        {/* Right Side — Feature Grid */}
        <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
          {features.map((feature, i) => (
            <div
              key={i}
              className="flex flex-col items-center justify-center p-6 bg-[#0A1124] rounded-2xl border border-white/5 hover:border-yellow-400/30 transition duration-300"
            >
              <div className="mb-3 p-3 rounded-xl bg-yellow-400/10">{feature.icon}</div>
              <p className="font-medium text-gray-200">{feature.title}</p>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
}
